<template>
  <section class="content">
    <div class="container-fluid">
        <div class="row">

          <div class="col-12">
        
            <div class="card" v-if="$gate.isAdmin()">
              <div class="card-header">
                <h3 class="card-title">Company List</h3>

                <div class="card-tools">
                  
                  <button type="button" class="btn btn-sm btn-primary" @click="newModal">
                      <i class="fa fa-plus-square"></i>
                      Add New
                  </button>
                </div>
              </div>
              <!-- /.card-header -->
              <div class="card-body table-responsive p-0">
                <table class="table table-hover">
                  <thead>
                    <tr>
                      <th>ID</th>
                      <th>Name</th> 
                      <th>Created</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                     <tr v-for="company in Companies.data" :key="company.id">

                      <td>{{company.id}}</td>
                      <td class="text-capitalize">{{company.name}}</td> 
                      <td>{{company.created_at|myDate}}</td>
                      <td>

                        <a href="javascript:void(0);" @click="editModal(company)" class="btn btn-success btn-sm">
                           Edit
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <!-- /.card-body -->
              <div class="card-footer">
                  <pagination :data="Companies" @pagination-change-page="getResults"></pagination>
              </div>
            </div>
            <!-- /.card -->
          </div>
        </div>


        <div v-if="!$gate.isAdmin()">
            <not-found></not-found>
        </div>

        <!-- Modal -->
        <div class="modal fade" id="addNewcompany" tabindex="-1" role="dialog" aria-labelledby="addNewcompany" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" v-show="!editmode">Create New company</h5>
                    <h5 class="modal-title" v-show="editmode">Update company</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>

                <!-- <form @submit.prevent="createUser"> -->

                <form @submit.prevent="editmode ? updatecompany() : createcompany()">
                    <div class="modal-body">
                        <div class="form-group">
                            <label>Name</label>
                            <input v-model="form.name" type="text" name="name"
                                class="form-control" :class="{ 'is-invalid': form.errors.has('name') }" ref="name">
                            <has-error :form="form" field="name"></has-error>
                        </div> 
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        <button v-show="editmode" type="submit" class="btn btn-success">Update</button>
                        <button v-show="!editmode" type="submit" class="btn btn-primary">Create</button>
                    </div>
                  </form>
                </div>
            </div>
        </div>
    </div>
  </section>
</template>

<script>
    export default {
        data () {
            return {
                editmode: false,
                Companies : {}, 
                form: new Form({
                    id : '',
                    name: '',
                    parent_id: '',
                })
            }
        },
        methods: {

            getResults(page = 1) {

                  this.$Progress.start();
                  
                  axios.get('/api/company?page=' + page).then(({ data }) => (this.Companies = data.data));

                  this.$Progress.finish();
            },
            updatecompany(){
                this.$Progress.start();
                this.form.put('/api/company/'+this.form.id)
                .then((response) => {
                    // success
                    $('#addNewcompany').modal('hide');
                    Toast.fire({
                      icon: 'success',
                      title: response.data.message
                    });
                    this.$Progress.finish();
                        //  Fire.$emit('AfterCreate');

                    this.loadCompanies();
                })
                .catch(() => {
                    this.$Progress.fail();
                });

            },
            editModal(company){
                this.editmode = true;
                this.form.reset();
                $('#addNewcompany').modal('show');
                 this.$refs.name.focus();
                this.form.fill(company);
            },
            newModal(){
                this.editmode = false;
                this.form.reset();
                $('#addNewcompany').modal('show');
                 this.$refs.name.focus();
            },

            loadCompanies(){
                if(this.$gate.isAdmin()){
                    axios.get("/api/company").then(({ data }) => (this.Companies = data.data)); 
                }
            },
            
            createcompany(){

                this.form.post('/api/company')
                .then((response)=>{
                    $('#addNewcompany').modal('hide');

                    Toast.fire({
                            icon: 'success',
                            title: response.data.message
                    });

                    this.$Progress.finish();
                    this.loadCompanies();
                })
                .catch(()=>{
                    Toast.fire({
                        icon: 'error',
                        title: 'Some error occured! Please try again'
                    });
                })
            },
             

        },
        mounted() {
           
        },
        created() {

            this.$Progress.start();
            this.loadCompanies();
            this.$Progress.finish();
        }
    }
</script>
